
import { defineComponent, ref, watch, PropType } from 'vue'
import { BaseForm, IForm } from '@vonor/ui'

import { createPageData, updatePageData } from '@/service/main/main'

export default defineComponent({
  components: {
    BaseForm
  },
  props: {
    modalConfig: {
      type: Object as PropType<IForm>,
      required: true
    },
    defaultInfo: {
      type: Object,
      default: () => ({})
    },
    pageName: {
      type: String,
      required: true
    },
    otherInfo: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['afterCreateData', 'afterdeleteData'],
  setup(props, { emit }) {
    const dialogVisible = ref(false)
    const formData = ref<any>({})

    watch(
      () => props.defaultInfo,
      (newValue) => {
        for (let item of props.modalConfig.formItems) {
          formData.value[`${item.field}`] = newValue[`${item.field}`]
        }
      }
    )

    // 新增|編輯
    const handleCreateData = async () => {
      await createPageData(`/${props.pageName}`, {
        ...formData.value,
        ...props.otherInfo
      })
      emit('afterCreateData')
    }

    const handledeleteData = async () => {
      await updatePageData(`/${props.pageName}/${props.defaultInfo.id}`, {
        ...formData.value,
        ...props.otherInfo
      })
      emit('afterdeleteData')
    }

    const handleConfirmClick = () => {
      dialogVisible.value = false
      if (Object.keys(props.defaultInfo).length) {
        handledeleteData()
      } else {
        handleCreateData()
      }
    }

    const otherSlotProps = props.modalConfig.formItems.filter((item: any) => {
      if (item.slotName) return true
    })

    return {
      dialogVisible,
      formData,
      handleConfirmClick,
      otherSlotProps
    }
  }
})
